
@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400,500,600,700");
html, body, #app {
  height: 100%;
}
.app-margin {
  margin: 10px;
}
.avatar {
  width: 30px;
  float: right;
  border-radius: 2px;
}
.btn.btn-default {
  color: #270f34;
}
.creds {
  position: absolute;
  bottom: 5px;
  right: 5px;
  text-align: right;
}
.list-group-item {
  color: #2c113a;
}
.page-header {
  margin-bottom: 0px;
}
.sign-out {
  float: right;
  padding-top: 8px;
  padding-right: 10px;
  font-size: 12px;
}
.user-info {
  margin-top: 5px;
  margin-bottom: 30px;
}



